.game-countdown {
  color: #333;
  text-decoration: none;
}
.game-countdown:active,
.game-countdown:focus,
.game-countdown:hover {
  color: #333;
}
.game-countdown.paused span {
  animation: blink 2s steps(2, start) infinite;
  -webkit-animation: blink 2s steps(2, start) infinite;
}
@keyframes blink {
  to {
    visibility: hidden;
  }
}
@-webkit-keyframes blink {
  to {
    visibility: hidden;
  }
}
.game-countdown.finished {
  animation: blink 1s steps(2, start) 10;
  -webkit-animation: blink 1s steps(2, start) 10;
}
@keyframes blink {
  to {
    visibility: hidden;
  }
}
@-webkit-keyframes blink {
  to {
    visibility: hidden;
  }
}
