#spymaster {
  border: 1px solid black;
  float: left;
  border-radius: 10px;
  position: relative;
  height: 12vw;
  width: 12vw;
  line-height: 12vw;
  vertical-align: middle;
  overflow: hidden;
  background-color: #ffd0d0;
  cursor: pointer;
  margin: 0 10vw 0 28vw;
}
#spymaster:hover {
  background-color: #d0d0ff;
}
#spymaster:before {
  content: '';
  position: absolute;
  left: 20%;
  width: 100%;
  height: 200%;
  background-color: #d0d0ff;
  top: 0;
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  transform: rotate(45deg);
}
#spymaster:hover:before {
  background-color: #ffd0d0;
}
#spymaster:after {
  content: 'Spymaster';
  left: 0;
  right: 0;
  position: absolute;
  height: 200px;
}
#operative {
  border-radius: 10px;
  position: relative;
  height: 12vw;
  width: 12vw;
  line-height: 12vw;
  vertical-align: middle;
  overflow: hidden;
  border: 1px solid black;
  cursor: pointer;
  margin: 0 20vw 0 10vw;
}
#operative:hover {
  -webkit-filter: invert(100%);
  filter: invert(100%);
  background-color: #d0d0ff;
}
#operative:hover:before {
  background-color: #ffd0d0;
}
#operative:after {
  content: 'Field Operative';
  left: 0;
  right: 0;
  position: absolute;
  height: 200px;
}
