body .select-button {
  width: 50px;
  height: 45px;
  line-height: 50px;
  text-align: center;
  border: 1px solid grey;
  cursor: pointer;
  border-radius: 50%;
}
body .select-button:active {
  -webkit-transform: translate(0px, 5px);
  -moz-transform: translate(0px, 5px);
  -ms-transform: translate(0px, 5px);
  -o-transform: translate(0px, 5px);
  -webkit-transition: 0.1s all ease-out;
  -moz-transition: 0.1s all ease-out;
  -ms-transition: 0.1s all ease-out;
  -o-transition: 0.1s all ease-out;
  transition: 0.1s all ease-out;
}
body .select-button-red {
  background: #f74d4d;
  background-image: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #f74d4d), color-stop(100%, #f86569));
  background-image: -moz-gradient(linear, left top, left bottom, color-stop(0%, #f74d4d), color-stop(100%, #f86569));
  box-shadow: 0 5px #e24f4f;
}
body .select-button-red:active {
  box-shadow: 0 0 #e24f4f;
}
body .select-button-blue {
  color: #fff;
  background: #4d4df7;
  background-image: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #4d4df7), color-stop(100%, #6965f8));
  background-image: -moz-gradient(linear, left top, left bottom, color-stop(0%, #4d4df7), color-stop(100%, #6965f8));
  box-shadow: 0 5px #4f4fe2;
}
body .select-button-blue:active {
  box-shadow: 0 0 #4f4fe2;
}
